body {
  letter-spacing: -.04em;
  color: #111;
  background-color: #f6f6f6;
  font-family: DM Sans, sans-serif;
  font-weight: 400;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

body.modal-open {
  height: 100vh;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

button {
  cursor: pointer;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header-wrapper {
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

@media screen and (min-width: 375px) {
  .mob-header {
    width: 375px;
  }
}

.header__logo {
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__logo {
    padding-top: 19px;
    padding-bottom: 25px;
  }
}

.menu {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: #111;
  align-items: center;
  gap: 24px;
  margin-left: 32px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

@media screen and (min-width: 768px) {
  .menu {
    display: flex;
  }
}

@media screen and (min-width: 1440px) {
  .menu {
    margin-left: 99px;
  }
}

.menu.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.menu__shopping {
  align-items: center;
  gap: 4px;
  display: flex;
}

.icon-cart {
  fill: currentColor;
}

.current {
  color: #111;
  background: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.icon-burger {
  fill: #000;
}

.menu-toggle :hover, .menu-toggle :focus {
  fill: #4f2ee8;
}

.header__group {
  justify-content: center;
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__group {
    gap: 24px;
  }
}

