body {
  font-family: DM Sans, sans-serif;
  font-weight: 400;
  letter-spacing: -0.04em;

  color: #111111;
  background-color: $primary-background-color;
  transition: background-color $cubic-time;

  &.modal-open {
    overflow: hidden;
    height: 100vh;
  }
}
/*        */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

/*        (ul) */
ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
/*        */
a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
/*      */
img {
  display: block;
  max-width: 100%;
  height: auto;
}
/*      address */
address {
  font-style: normal;
}

/* For smooth page scrolling */
html {
  scroll-behavior: smooth;
}

button {
  cursor: pointer;
}
